:root {
	/* Spacing */
	--grid-base: 8px;
	--spacing-regular: var(--grid-base);
	--spacing-large: calc(var(--grid-base) * 2);
	--touch-target-min: 48px;

	/* Fonts */
	--font-size-base: 16px;
	--font-size-small: 14px;
	--font-size-title: 20px;
	--font-size-icon: 24px;
	--line-height-base: 24px;

	/* Color hex values */
	--color-dark: #333;
	--color-text-primary: #000000;
	--color-text-faded: #5c6c7c;
	--color-text-inverted: #ffffff;
	--color-highlight: #006be6;
	--color-highlight-border: hsl(212.08695652173913, 100%, 58.0980392157%);
	--color-success: #188701;
	--color-error: #eb1313;
	--color-background: #ffffff;
	--color-background-inverted: #5c6c7c;
	--color-background-faded: #f4f6f7;
	--color-border: #d6dde2;
	--color-border-strong: #5c6c7c;
	--color-border-invalid: hsl(0, 76.0393700787%, 86.8039215686%);
	--color-border-valid: hsl(109.70149253731344, 66.5294117647%, 81.6666666667%);
	--color-box-shadow: rgba(0, 0, 0, 0.2);
	--color-box-shadow-highlight: rgba(0, 107, 230, 0.2);
	--color-box-shadow-strong: rgba(0, 0, 0, 0.5);
	--color-box-shadow-focus: rgba(0, 107, 230, 0.2);
	--color-box-shadow-invalid: rgba(235, 19, 19, 0.2);
	--color-box-shadow-valid: rgba(24, 135, 1, 0.2);

	/* Border and shadow */
	--box-shadow-dimensions-small: 0 2px 2px 0;
	--box-shadow-dimensions-large: 0 9px 16px 0;
	--border-width: 1px;
	--border-width-emphasized: 2px;
	--border-radius: 4px;

	/* Transitions */
	--transition-duration: 0.25s;
	--transition-easing: ease;

	/* Z-index levels */
	--zindex-base: 0;
	--zindex-tooltip: 100;
	--zindex-dropdown: 200;
	--zindex-fixed: 300;
	--zindex-dialog: 400;
	--zindex-alert: 500;
}

.root,
.root *,
.root *::before,
.root *::after {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}

.root {
	border: 1px solid #d6dde2;
	border: var(--border-width) solid var(--color-border);
	display: inline-block;
	font-size: 16px;
	font-size: var(--font-size-base);
	line-height: 24px;
	line-height: var(--line-height-base);
	margin: 0 calc(calc(8px * 2) + 2px);
	margin: 0 calc(var(--spacing-large) + 2px);
	position: relative;
	width: calc((8px * 35) + (1px * 4));
	width: calc((var(--grid-base) * 35) + (var(--border-width) * 4));
}

.dateGrid {
	width: 100%;
}

/* Date grid */

.weekday, .date {
	text-align: center;
	width: 14.28%;
}

.weekday {
	background-color: #f4f6f7;
	background-color: var(--color-background-faded);
	border-bottom: 1px solid #d6dde2;
	border-bottom: var(--border-width) solid var(--color-border);
	color: #5c6c7c;
	color: var(--color-text-faded);
	font-weight: normal;
	padding: 0 calc(8px / 2);
	padding: 0 calc(var(--grid-base) / 2);
	text-align: center;
	font-size: 14px;
	font-size: var(--font-size-small);
	text-transform: uppercase;
}

.weekday abbr {
	text-decoration: none;
}

.date {
	color: #5c6c7c;
	color: var(--color-text-faded);
	cursor: pointer;
	font-weight: bold;
	outline: 1px solid transparent;
	outline: var(--border-width) solid transparent;
	padding: 8px;
	padding: var(--grid-base);
	-webkit-transition: color 0.25s ease, background-color 0.25s ease;
	transition: color 0.25s ease, background-color 0.25s ease;
	-webkit-transition: color var(--transition-duration) var(--transition-easing), background-color var(--transition-duration) var(--transition-easing);
	transition: color var(--transition-duration) var(--transition-easing), background-color var(--transition-duration) var(--transition-easing);
}

.todayDate {
	background-color: #f4f6f7;
	background-color: var(--color-background-faded);
}

.date:focus,
.date:hover {
	color: #006be6;
	color: var(--color-highlight);
	outline: 1px solid #d6dde2;
	outline: var(--border-width) solid var(--color-border);
}

.inactiveDate {
	color: #d6dde2;
	color: var(--color-border);
}

.selectedDate {
	background-color: #006be6;
	background-color: var(--color-highlight);
	color: #ffffff;
	color: var(--color-text-inverted);
}

.selectedDate:focus,
.selectedDate:hover {
	background-color: #ffffff;
	background-color: var(--color-background);
	color: #006be6;
	color: var(--color-highlight);
	outline: 1px solid #006be6;
	outline: var(--border-width) solid var(--color-highlight);
}

/* Date picker  */

.topMatter {
	background: #f4f6f7;
	background: var(--color-background-faded);
	border-bottom: 1px solid #d6dde2;
	border-bottom: var(--border-width) solid var(--color-border);
}

.monthTrigger,
.yearTrigger,
.previous,
.next {
	background: 0;
	border: 1px solid transparent;
	border: var(--border-width) solid transparent;
	color: inherit;
	cursor: pointer;
	font-family: inherit;
	line-height: inherit;
	outline: none;
	-webkit-transition: border-color 0.25s ease;
	transition: border-color 0.25s ease;
	-webkit-transition: border-color var(--transition-duration) var(--transition-easing);
	transition: border-color var(--transition-duration) var(--transition-easing);
}

.monthTrigger:focus,
.monthTrigger:hover,
.yearTrigger:focus,
.yearTrigger:hover,
.previous:focus,
.previous:hover,
.next:focus,
.next:hover {
	border-color: #006be6;
	border-color: var(--color-highlight);
}

.monthTrigger,
.yearTrigger {
	color: #000000;
	color: var(--color-text-primary);
	font-size: 14px;
	font-size: var(--font-size-small);
	padding: 8px;
	padding: var(--grid-base);
	text-transform: uppercase;
}

.monthTriggerActive,
.yearTriggerActive {
	border-color: transparent;
	border-bottom-color: #006be6;
	border-bottom-color: var(--color-highlight);
	color: #006be6;
	color: var(--color-highlight);
}

.previous,
.next {
	background-color: #f4f6f7;
	background-color: var(--color-background-faded);
	border: 1px solid #d6dde2;
	border: var(--border-width) solid var(--color-border);
	color: #000000;
	color: var(--color-text-primary);
	display: block;
	font-size: 24px;
	font-size: var(--font-size-icon);
	line-height: 1;
	padding: calc(8px * 1.5) 0;
	padding: calc(var(--grid-base) * 1.5) 0;
	position: absolute;
	top: 50%;
	width: calc(calc(8px * 2) + 2px);
	width: calc(var(--spacing-large) + 2px);
}

.previous i,
.next i {
	position: relative;
	left: calc((calc(8px * 2) - 24px) / 2);
	left: calc((var(--spacing-large) - var(--font-size-icon)) / 2);
}

.previous {
	left: 0;
	-webkit-transform: translate(-100%, -50%);
	        transform: translate(-100%, -50%);
}

.next {
	right: 0;
	-webkit-transform: translate(100%, -50%);
	        transform: translate(100%, -50%);
}

.monthGrid,
.yearGrid {
	position: relative;
	background-color: #ffffff;
	background-color: var(--color-background);
}

.monthFields,
.yearFields {
	border: 0;
	margin: 0;
	padding: 0;
}

.monthRadio,
.yearRadio {
	display: block;
	float: left;
	position: relative;
	text-align: center;
	text-transform: capitalize;
}

.monthRadio {
	width: 33.33%;
}

.yearRadio {
	width: 20%;
}

.monthRadioLabel,
.yearRadioLabel {
	border: 1px solid transparent;
	border: var(--border-width) solid transparent;
	cursor: pointer;
	display: block;
	text-decoration: none;
	padding: 8px;
	padding: var(--grid-base);
	-webkit-transition: color 0.25s ease, background-color 0.25s ease;
	transition: color 0.25s ease, background-color 0.25s ease;
	-webkit-transition: color var(--transition-duration) var(--transition-easing), background-color var(--transition-duration) var(--transition-easing);
	transition: color var(--transition-duration) var(--transition-easing), background-color var(--transition-duration) var(--transition-easing);
}

.monthRadioChecked .monthRadioLabel,
.yearRadioChecked .yearRadioLabel {
	background-color: #006be6;
	background-color: var(--color-highlight);
	color: #ffffff;
	color: var(--color-text-inverted);
}

.monthRadioInput,
.yearRadioInput {
	height: 100%;
	left: 0;
	margin: 0;
	opacity: 0;
	position: absolute;
	top: 0;
	width: 100%;
}

.monthRadio:hover .monthRadioLabel,
.yearRadio:hover .yearRadioLabel {
	color: #006be6;
	color: var(--color-highlight);
	border: 1px solid #d6dde2;
	border: var(--border-width) solid var(--color-border);
}

.monthRadioChecked:hover .monthRadioLabel,
.yearRadioChecked:hover .yearRadioLabel,
.monthRadioInput:focus ~ .monthRadioLabel,
.yearRadioInput:focus ~ .yearRadioLabel {
	background-color: #ffffff;
	background-color: var(--color-background);
	color: #006be6;
	color: var(--color-highlight);
	border: 1px solid #006be6;
	border: var(--border-width) solid var(--color-highlight);
}



