:root {
	/* Spacing */
	--grid-base: 8px;
	--spacing-regular: var(--grid-base);
	--spacing-large: calc(var(--grid-base) * 2);
	--touch-target-min: 48px;

	/* Fonts */
	--font-size-base: 16px;
	--font-size-small: 14px;
	--font-size-title: 20px;
	--font-size-icon: 24px;
	--line-height-base: 24px;

	/* Color hex values */
	--color-dark: #333;
	--color-text-primary: #000000;
	--color-text-faded: #5c6c7c;
	--color-text-inverted: #ffffff;
	--color-highlight: #006be6;
	--color-highlight-border: hsl(212.08695652173913, 100%, 58.0980392157%);
	--color-success: #188701;
	--color-error: #eb1313;
	--color-background: #ffffff;
	--color-background-inverted: #5c6c7c;
	--color-background-faded: #f4f6f7;
	--color-border: #d6dde2;
	--color-border-strong: #5c6c7c;
	--color-border-invalid: hsl(0, 76.0393700787%, 86.8039215686%);
	--color-border-valid: hsl(109.70149253731344, 66.5294117647%, 81.6666666667%);
	--color-box-shadow: rgba(0, 0, 0, 0.2);
	--color-box-shadow-highlight: rgba(0, 107, 230, 0.2);
	--color-box-shadow-strong: rgba(0, 0, 0, 0.5);
	--color-box-shadow-focus: rgba(0, 107, 230, 0.2);
	--color-box-shadow-invalid: rgba(235, 19, 19, 0.2);
	--color-box-shadow-valid: rgba(24, 135, 1, 0.2);

	/* Border and shadow */
	--box-shadow-dimensions-small: 0 2px 2px 0;
	--box-shadow-dimensions-large: 0 9px 16px 0;
	--border-width: 1px;
	--border-width-emphasized: 2px;
	--border-radius: 4px;

	/* Transitions */
	--transition-duration: 0.25s;
	--transition-easing: ease;

	/* Z-index levels */
	--zindex-base: 0;
	--zindex-tooltip: 100;
	--zindex-dropdown: 200;
	--zindex-fixed: 300;
	--zindex-dialog: 400;
	--zindex-alert: 500;
}

.root,
.root *,
.root *::before,
.root *::after {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}

.root {
	display: block;
	font-size: 16px;
	font-size: var(--font-size-base);
	line-height: 24px;
	line-height: var(--line-height-base);
	min-height: 24px;
	min-height: var(--line-height-base);
	padding: 0 0 0 calc(8px * 3);
	padding: 0 0 0 calc(var(--grid-base) * 3);
	position: relative;
}

.input {
	height: 100%;
	left: 0;
	margin: 0;
	opacity: 0;
	position: absolute;
	top: 0;
	width: 100%;
}

.inputWrapper {
	composes: checkIcon icon from './icon.m.css';
	left: 0;
	position: absolute;
	top: calc(8px / 2);
	top: calc(var(--grid-base) / 2);
}

.inputWrapper::before,
.inputWrapper::after {
	display: block;
	height: calc(8px * 2);
	height: calc(var(--grid-base) * 2);
	width: calc(8px * 2);
	width: calc(var(--grid-base) * 2);
}

.inputWrapper::after {
	background-color: #ffffff;
	background-color: var(--color-background);
	border: 1px solid #5c6c7c;
	border: var(--border-width) solid var(--color-border-strong);
	content: '';
}

.inputWrapper::before {
	color: #5c6c7c;
	color: var(--color-border-strong);
	font-size: 14px;
	font-size: var(--font-size-small);
	left: 0;
	line-height: calc(8px * 2);
	line-height: calc(var(--grid-base) * 2);
	position: absolute;
	text-align: center;
	top: 0;
	-webkit-transform: scale(0);
	        transform: scale(0);
	-webkit-transition: -webkit-transform 0.25s ease;
	transition: -webkit-transform 0.25s ease;
	transition: transform 0.25s ease;
	transition: transform 0.25s ease, -webkit-transform 0.25s ease;
	-webkit-transition: -webkit-transform var(--transition-duration) var(--transition-easing);
	transition: -webkit-transform var(--transition-duration) var(--transition-easing);
	transition: transform var(--transition-duration) var(--transition-easing);
	transition: transform var(--transition-duration) var(--transition-easing), -webkit-transform var(--transition-duration) var(--transition-easing);
	width: 100%;
}

/* checked style */

.checked .inputWrapper::before {
	-webkit-transform: scale(1);
	        transform: scale(1);
}

/* Toggle mode */

.toggle {
	padding-left: 0;
}

.toggle span {
 color: #000000;
 color: var(--color-text-primary);
}

.toggle .inputWrapper {
	width: auto;
	position: static;
	display: inline-block;
	padding-right: 8px;
	padding-right: var(--grid-base);
}

.toggle .inputWrapper::before,
.toggle .inputWrapper::after {
	display: none;
}

.toggleSwitch {
	background-color: #d6dde2;
	background-color: var(--color-border);
	border: 1px solid #5c6c7c;
	border: var(--border-width) solid var(--color-border-strong);
	border-radius: calc(24px / 2);
	border-radius: calc(var(--line-height-base) / 2);
	display: inline-block;
	height: 24px;
	height: var(--line-height-base);
	position: relative;
	-webkit-transition: background 0.25s ease, border 0.25s ease;
	transition: background 0.25s ease, border 0.25s ease;
	-webkit-transition: background var(--transition-duration) var(--transition-easing), border var(--transition-duration) var(--transition-easing);
	transition: background var(--transition-duration) var(--transition-easing), border var(--transition-duration) var(--transition-easing);
	vertical-align: middle;
	width: calc(24px * 1.75);
	width: calc(var(--line-height-base) * 1.75);
}

.toggleSwitch::before,
.toggleSwitch::after {
	border-radius: calc(24px / 2);
	border-radius: calc(var(--line-height-base) / 2);
	content: "";
	height: 24px;
	height: var(--line-height-base);
	left: calc(1px * -1);
	left: calc(var(--border-width) * -1);
	position: absolute;
	top: calc(1px * -1);
	top: calc(var(--border-width) * -1);
	-webkit-transition: background-color 0.25s ease, -webkit-transform 0.25s ease;
	transition: background-color 0.25s ease, -webkit-transform 0.25s ease;
	transition: background-color 0.25s ease, transform 0.25s ease;
	transition: background-color 0.25s ease, transform 0.25s ease, -webkit-transform 0.25s ease;
	-webkit-transition: background-color var(--transition-duration) var(--transition-easing), -webkit-transform var(--transition-duration) var(--transition-easing);
	transition: background-color var(--transition-duration) var(--transition-easing), -webkit-transform var(--transition-duration) var(--transition-easing);
	transition: background-color var(--transition-duration) var(--transition-easing), transform var(--transition-duration) var(--transition-easing);
	transition: background-color var(--transition-duration) var(--transition-easing), transform var(--transition-duration) var(--transition-easing), -webkit-transform var(--transition-duration) var(--transition-easing);
	width: 24px;
	width: var(--line-height-base);
}

.toggleSwitch::before {
	background-color: #ffffff;
	background-color: var(--color-background);
	border: 1px solid #5c6c7c;
	border: var(--border-width) solid var(--color-border-strong);
}

.toggleSwitch::after {
	background-color: #006be6;
	background-color: var(--color-highlight);
	-webkit-transform: translate(0, 0) scale(0);
	        transform: translate(0, 0) scale(0);
}

.onLabel,
.offLabel {
	color: #5c6c7c;
	color: var(--color-text-faded);
	display: inline-block;
}

.onLabel {
	padding-left: 8px;
	padding-left: var(--spacing-regular);
}

.offLabel {
	padding-right: 8px;
	padding-right: var(--spacing-regular);
}

.checked .toggleSwitch {
	background-color: #006be6;
	background-color: var(--color-highlight);
	border-color: #006be6;
	border-color: var(--color-highlight);
}

.checked .toggleSwitch::before {
	border-color: #006be6;
	border-color: var(--color-highlight);
	-webkit-transform: translate(75%, 0);
	        transform: translate(75%, 0);
}

.checked .toggleSwitch::after {
	border-color: #006be6;
	border-color: var(--color-highlight);
	-webkit-transform: translate(75%, 0) scale(0);
	        transform: translate(75%, 0) scale(0);
}

/* Focused styles */

.focused span {
	color: #006be6;
	color: var(--color-highlight);
}

.focused.toggle span {
	color: #000000;
	color: var(--color-text-primary);
}

.focused .inputWrapper::before {
	color: #006be6;
	color: var(--color-highlight);
}

.focused .inputWrapper::after {
	border: 2px solid #006be6;
	border: var(--border-width-emphasized) solid var(--color-highlight);
	-webkit-box-shadow: 0 2px 2px 0 rgba(0, 107, 230, 0.2);
	        box-shadow: 0 2px 2px 0 rgba(0, 107, 230, 0.2);
	-webkit-box-shadow: var(--box-shadow-dimensions-small) var(--color-box-shadow-highlight);
	        box-shadow: var(--box-shadow-dimensions-small) var(--color-box-shadow-highlight);
}

.focused .toggleSwitch::before {
	border-color: #006be6;
	border-color: var(--color-highlight);
	-webkit-box-shadow: 0 2px 2px 0 rgba(0, 107, 230, 0.2);
	        box-shadow: 0 2px 2px 0 rgba(0, 107, 230, 0.2);
	-webkit-box-shadow: var(--box-shadow-dimensions-small) var(--color-box-shadow-highlight);
	        box-shadow: var(--box-shadow-dimensions-small) var(--color-box-shadow-highlight);
}

.focused .toggleSwitch::after {
	-webkit-transform: translate(0, 0) scale(0.5);
	        transform: translate(0, 0) scale(0.5);
}

.focused.checked .toggleSwitch {
	-webkit-box-shadow: 0 2px 2px 0 rgba(0, 107, 230, 0.2);
	        box-shadow: 0 2px 2px 0 rgba(0, 107, 230, 0.2);
	-webkit-box-shadow: var(--box-shadow-dimensions-small) var(--color-box-shadow-highlight);
	        box-shadow: var(--box-shadow-dimensions-small) var(--color-box-shadow-highlight);
}

.focused.checked .toggleSwitch::after {
	-webkit-transform: translate(75%, 0) scale(0.5);
	        transform: translate(75%, 0) scale(0.5);
}

.focused .onLabel,
.focused .offLabel {
	color: #006be6;
	color: var(--color-highlight);
}

/* disabled and readonly styles */

.disabled .inputWrapper::after,
.readonly .inputWrapper::after {
	background-color: #f4f6f7;
	background-color: var(--color-background-faded);
	border-color: #d6dde2;
	border-color: var(--color-border);
}

.disabled .toggleSwitch,
.readonly .toggleSwitch {
	background-color: #f4f6f7;
	background-color: var(--color-background-faded);
	border-color: #5c6c7c;
	border-color: var(--color-border-strong);
}

.disabled .toggleSwitch::before,
.readonly .toggleSwitch::before {
	background-color: #f4f6f7;
	background-color: var(--color-background-faded);
	border-color: #5c6c7c;
	border-color: var(--color-border-strong);
}

/* invalid style */

.invalid .inputWrapper::after {
	border-color: #eb1313;
	border-color: var(--color-error);
}

.invalid .inputWrapper::before {
	color: #eb1313;
	color: var(--color-error);
}

/* valid style */

.valid .inputWrapper::after {
	border-color: #188701;
	border-color: var(--color-success);
}

.valid .inputWrapper::before {
	color: #188701;
	color: var(--color-success);
}



